// lib/api.ts
const JIKAN_API_BASE = 'https://api.jikan.moe/v4'

// Interface Anime
export interface Anime {
  mal_id: number
  title: string
  title_english?: string
  images: {
    jpg: {
      image_url: string
      small_image_url: string
      large_image_url: string
    }
  }
  synopsis?: string
  episodes?: number | null
  status?: string
  score?: number
  genres?: Array<{ mal_id: number; name: string }>
  studios?: Array<{ mal_id: number; name: string }>
  year?: number | null
  aired?: {
    from: string
    to?: string | null
  }
}

// Response interface
export interface AnimeResponse {
  data: Anime[]
  pagination?: {
    last_visible_page: number
    has_next_page: boolean
    current_page: number
  }
}

// Optional rate limit helper
async function rateLimit() {
  return new Promise((resolve) => setTimeout(resolve, 300))
}

// Fetch top anime
export async function getTopAnime(page = 1): Promise<AnimeResponse> {
  try {
    await rateLimit()
    const res = await fetch(`${JIKAN_API_BASE}/top/anime?page=${page}&limit=25`)
    if (!res.ok) throw new Error('Failed to fetch top anime')
    return res.json()
  } catch (error) {
    console.error('Error fetching top anime:', error)
    return { data: [] }
  }
}

// Fetch anime detail by MAL ID
export async function getAnimeDetail(mal_id: number): Promise<Anime | null> {
  try {
    await rateLimit()
    const res = await fetch(`${JIKAN_API_BASE}/anime/${mal_id}`)
    if (!res.ok) throw new Error('Failed to fetch anime detail')
    const data = await res.json()
    return data.data
  } catch (error) {
    console.error('Error fetching anime detail:', error)
    return null
  }
}

// Fetch popular anime
export async function getPopularAnime(page = 1): Promise<AnimeResponse> {
  try {
    await rateLimit()
    const res = await fetch(`${JIKAN_API_BASE}/anime?order_by=popularity&sort=asc&page=${page}&limit=25`)
    if (!res.ok) throw new Error('Failed to fetch popular anime')
    return res.json()
  } catch (error) {
    console.error('Error fetching popular anime:', error)
    return { data: [] }
  }
}

// Fetch anime by genre
export async function getAnimeByGenre(genreId: number, page = 1): Promise<AnimeResponse> {
  try {
    await rateLimit()
    const res = await fetch(`${JIKAN_API_BASE}/anime?genres=${genreId}&page=${page}&limit=25`)
    if (!res.ok) throw new Error('Failed to fetch anime by genre')
    return res.json()
  } catch (error) {
    console.error('Error fetching anime by genre:', error)
    return { data: [] }
  }
}

// Search anime
export async function searchAnime(query: string, page = 1): Promise<AnimeResponse> {
  try {
    await rateLimit();
    const url = `${JIKAN_API_BASE}/anime?q=${encodeURIComponent(query)}&page=${page}&limit=25`;
    const res = await fetch(url);

    if (!res.ok) {
      const text = await res.text(); // baca responsenya biar bisa debug
      console.error('Failed search response:', text);
      throw new Error(`Failed to search anime: ${res.status} ${res.statusText}`);
    }

    const data = await res.json();
    return data;
  } catch (error) {
    console.error('Error searching anime:', error);
    return { data: [] };
  }
}

// Get genres
export async function getGenres() {
  try {
    await rateLimit()
    const res = await fetch(`${JIKAN_API_BASE}/genres/anime`)
    if (!res.ok) throw new Error('Failed to fetch genres')
    const data = await res.json()
    return data.data
  } catch (error) {
    console.error('Error fetching genres:', error)
    return []
  }
}
