'use client'

import { useState, useEffect, useRef } from 'react'
import Link from 'next/link'
import { Search, Menu, X } from 'lucide-react'
import { Anime, searchAnime } from '@/lib/api'
import { ThemeToggle } from '@/components/theme-toggle'
import debounce from 'lodash.debounce'

export function Navbar() {
  const [isOpen, setIsOpen] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [results, setResults] = useState<Anime[]>([])
  const [loading, setLoading] = useState(false)
  const dropdownRef = useRef<HTMLDivElement>(null)

  // Debounced search function
  const handleSearch = debounce(async (query: string) => {
    if (!query) {
      setResults([])
      return
    }
    setLoading(true)
    const data = await searchAnime(query)
    setResults(data.data)
    setLoading(false)
  }, 400)

  useEffect(() => {
    handleSearch(searchQuery)
  }, [searchQuery])

  // Close dropdown on click outside
  useEffect(() => {
    const handleClickOutside = (e: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(e.target as Node)) {
        setResults([])
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  return (
    <nav className="sticky top-0 z-50 bg-card/95 backdrop-blur border-b border-border">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <Link href="/" className="flex items-center gap-2 group">
            <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center neon-glow">
              <span className="text-white font-bold text-lg">A</span>
            </div>
            <span className="text-xl font-bold text-foreground hidden sm:inline group-hover:text-primary transition-colors">
              Stream4nzu
            </span>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center gap-8">
            <Link href="/" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Home</Link>
            <Link href="/popular" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Popular</Link>
            <Link href="/trending" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Trending</Link>
            <Link href="/genres" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Genres</Link>
          </div>

          {/* Search Bar */}
          <div className="relative hidden sm:flex items-center gap-2 bg-input border border-border rounded-lg px-3 py-2 w-64">
            <Search size={18} className="text-muted-foreground" />
            <input
              type="text"
              placeholder="Search anime..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="bg-transparent outline-none text-sm flex-1 placeholder:text-muted-foreground"
            />

            {/* Live Search Dropdown */}
            {results.length > 0 && (
              <div
                ref={dropdownRef}
                className="absolute top-full left-0 w-full mt-1 bg-card border border-border rounded-md shadow-lg max-h-80 overflow-y-auto z-50"
              >
                {results.slice(0, 10).map((anime) => (
                  <Link
                    key={anime.mal_id}
                    href={`/anime/${anime.mal_id}`}
                    className="flex items-center gap-3 px-3 py-2 hover:bg-primary/10 transition-colors"
                    onClick={() => setResults([])}
                  >
                    <img
                      src={anime.images.jpg.small_image_url || '/placeholder.svg'}
                      alt={anime.title}
                      className="w-10 h-14 object-cover rounded"
                    />
                    <div className="flex flex-col">
                      <span className="text-sm font-medium text-foreground line-clamp-1">{anime.title}</span>
                      <span className="text-xs text-muted-foreground">{anime.episodes ?? 'TBA'} Episodes</span>
                    </div>
                  </Link>
                ))}
                <Link
                  href={`/search?q=${encodeURIComponent(searchQuery)}`}
                  className="block text-center py-2 text-sm text-primary font-medium hover:bg-primary/5"
                >
                  See all results
                </Link>
              </div>
            )}
          </div>

          <div className="flex items-center gap-2">
            <ThemeToggle />
            {/* Mobile Menu Button */}
            <button
              className="md:hidden p-2 hover:bg-secondary rounded-lg transition-colors"
              onClick={() => setIsOpen(!isOpen)}
            >
              {isOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>
        </div>
      </div>
    </nav>
  )
}
