'use client'

import { useState, useEffect } from 'react'
import Image from 'next/image'
import { ChevronLeft, ChevronRight, Play } from 'lucide-react'
import { Anime } from '@/lib/api'
import { Button } from '@/components/ui/button'

interface HeroCarouselProps {
  animes: Anime[]
  height?: string // optional, default height
}

export function HeroCarousel({ animes, height = 'h-64 md:h-72' }: HeroCarouselProps) {
  const [current, setCurrent] = useState(0)
  const [autoPlay, setAutoPlay] = useState(true)

  useEffect(() => {
    if (!autoPlay || animes.length === 0) return

    const timer = setInterval(() => {
      setCurrent((prev) => (prev + 1) % animes.length)
    }, 5000)

    return () => clearInterval(timer)
  }, [autoPlay, animes.length])

  const anime = animes[current]
  if (!anime) return null

  const next = () => setCurrent((prev) => (prev + 1) % animes.length)
  const prev = () => setCurrent((prev) => (prev - 1 + animes.length) % animes.length)

  return (
    <div
      className={`relative w-full overflow-hidden rounded-lg md:rounded-none ${height}`}
      onMouseEnter={() => setAutoPlay(false)}
      onMouseLeave={() => setAutoPlay(true)}
    >
      {/* Background */}
      <div className="absolute inset-0">
        <Image
          src={anime.images.jpg.large_image_url || "/placeholder.svg"}
          alt={anime.title}
          fill
          className="object-cover"
          priority
        />
        <div className="absolute inset-0 gradient-overlay" />
      </div>

      {/* Content */}
      <div className="relative h-full flex flex-col justify-end p-6 md:p-12 text-white">
        <div className="max-w-2xl">
          <h1 className="text-2xl md:text-3xl font-bold mb-2">{anime.title}</h1>
          <p className="text-sm md:text-base mb-2 text-muted-foreground">
            {anime.status} • {anime.episodes} Episodes • Score: {anime.score}
          </p>
          <p className="text-sm md:text-base line-clamp-3 mb-4 text-foreground/80">
            {anime.synopsis}
          </p>
          <div className="flex gap-3">
            <Button className="bg-primary hover:bg-primary/90 text-white gap-2">
              <Play size={18} /> Watch Now
            </Button>
            <Button variant="outline" className="border-primary text-primary hover:bg-primary/10">
              Add to Favorites
            </Button>
          </div>
        </div>
      </div>

      {/* Navigation */}
      <button
        onClick={prev}
        className="absolute left-4 top-1/2 -translate-y-1/2 z-10 p-2 bg-black/50 hover:bg-black/75 rounded-full transition-colors"
      >
        <ChevronLeft className="text-white" size={24} />
      </button>
      <button
        onClick={next}
        className="absolute right-4 top-1/2 -translate-y-1/2 z-10 p-2 bg-black/50 hover:bg-black/75 rounded-full transition-colors"
      >
        <ChevronRight className="text-white" size={24} />
      </button>

      {/* Indicators */}
      <div className="absolute bottom-2 left-1/2 -translate-x-1/2 flex gap-2 z-10">
        {animes.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrent(index)}
            className={`h-2 rounded-full transition-all ${
              index === current ? 'bg-primary w-6 neon-glow' : 'bg-white/40 w-2 hover:bg-white/60'
            }`}
          />
        ))}
      </div>
    </div>
  )
}
